.MyTasks{
    height: fit-content;
    grid-row-start:1;
    grid-column-start: 14;
    grid-column-end:47;
    margin-top: 40%;
    padding: 0;
    color: black;
}
@media(min-device-width:1920px){
    .MyTasks{
        margin-top: 35%;
    }
}
@media(max-device-width:1024px){
    .MyTasks{
        margin-top: 60%;
    }
}
@media(max-device-width:768px){
    .MyTasks{
        margin-top: 72%;
    }
}
@media(max-device-width:591px){
    .MyTasks{
        margin-top: 106%;
    }
}

@media(max-device-width:425px){
    .MyTasks{ 
        grid-column-start: 3;
        grid-column-end: 46;
        margin-top: 93%;
    }
}