.todoItem{
    display: flex;
    margin-bottom: 3%;
    height: fit-content;
    padding-right: 15px;
    justify-content: space-between;
    align-items: center;
    border: 1px solid;
    border-radius: 10px;
    border-color: white;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
    font-weight: 400;
    padding: 2% 1%;
    box-sizing:border-box
}
.flex{
    display: flex;
    align-items: center;
    width: 96%;
    height: fit-content;
    box-sizing:border-box;
    
}

.checkbox{
    width:32px;
    height: 32px;
    border:1px solid #B9BDCB;
    border-radius:6px;
  
}
.checkbox:checked{
   background: url('../../assets/checkbox_done.svg') no-repeat;
}


.task{
    margin-left: 2%;
    word-break: break-all;
}


.deletBtn{
    width:20px;
    height: 20px;
    background: url('../../assets/button_delet.svg') no-repeat;
    border:none
}
.done{
    color:#8A8E9B;
  
}

