.header{
    grid-column-start:14;
    grid-column-end: 31;
    grid-row-start:1;
    font-family: 'Montserrat', sans-serif;
    font-weight: 800;
    box-sizing:border-box;
    height: fit-content;

}
@media(min-device-width:1920px){
    .header{
        grid-column-end: 27;
    }
}
@media(max-device-width:1024px){
    .header{
        grid-column-end: 38;
    }
}

@media(max-device-width:768px){
    .header{
        grid-column-end: 46;
    }
}

@media(max-device-width:425px){
    .header{
        grid-column-start:5;
        grid-column-end: 44;
        text-align: center;
    }
}

.title{
    font-size: 48px;
    font-weight: 800;
}
@media(max-device-width:591px){
    .title{
        font-size: 38px;
    }
}



.subTitle{
   
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    font-weight: 400;
    box-sizing:border-box
    
}
@media(max-device-width:591px){
    .subTitle{
        font-size: 15px;
    }
}


