.form{
    grid-column-start: 14;
    grid-column-end: 47;
    height: fit-content;
    display: flex;
    justify-content:space-between;
    align-items: center;
    margin-top: 24%;
    grid-row-start:1 ;
}
@media(min-device-width:1920px){
    .form{
        margin-top: 20%;
    }
}
@media(max-device-width:1024px){
    .form{
        margin-top: 36%;
    }
}
@media(max-device-width:768px){
    .form{
        margin-top: 43%;
    }
}
@media(max-device-width:591px){
    .form{
        margin-top: 65%;
    }
}
@media(max-device-width:425px){
    .form{
        grid-column-start: 3;
        grid-column-end: 46;
        margin-top: 57%;
    }
}


.input{
    width: 85%;
    height: 78px;
    font-size: 20px;
    font-weight: 400;
    font-family: 'Montserrat', sans-serif;
    color:#002140;
    outline: none;
    cursor: pointer;
    border-radius:10px;
    background:#FFFFFF;
    border: 1px solid #B9BDCB;
    padding-left:30px;
}

@media(max-device-width:1024px){
    .input{
        width: 76%;
    } 
}
@media(max-device-width:768px){
    .input{
        width: 69%;
    } 
}
@media(max-device-width:425px){
    .input{
        width: 70%;
        padding-left: 6px;
    } 
}
@media(max-device-width:375px){
    .input{
        width: 63%;
        padding-left: 10px;
    } 
}



.input::placeholder{
    font-size: 20px;
    font-weight: 400;
    font-family: 'Montserrat', sans-serif;

}

.btn{
    width:90px;
    height: 78px; 
    background: url('../../assets/button_add.svg') no-repeat;;
    border-radius:10px;
    border:none
}

