body{
  margin: 0;
}
.grid{
    height: 100vh;
    display: grid;
    box-sizing:border-box;
    grid-template-columns: 1fr repeat(46, 1fr) ; 
   
  }


.logo{
  background:url('./assets/logo.svg') no-repeat;
  width:166px;
  height: 30px;
}
@media(max-device-width:748px){
  .logo{
    transform:rotate(90deg);
  }
}
  .Bg{
    padding-top: 16%;
    padding-left: 10%;
    grid-column-start: 1;
    grid-column-end: 12;
    height: 100%;
    background-image: url('./assets/bg_left.svg');
    
  }

  @media(max-device-width:768px){
    .Bg{
      padding-top: 30%;
      padding-left: 4%;
    }
}
@media(max-device-width:748px){
  .Bg{
    padding-top: 50%;
    padding-left: 0;
  }
}
@media(max-device-width:591px){
  .Bg{
    padding-top: 80%;
   
  }
}

@media(max-device-width:425px){
  .Bg{
   display: none;
  }
}

  .noTasks{
    margin-top: 14em;
    grid-row-start:1 ;
    grid-column-start: 14;
    grid-column-end: 24;
    font-size: 30px;
    font-weight: 800;
    font-family: 'Montserrat', sans-serif;
  }